import "./App.css";
import * as React from "react";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Button from "@mui/material/Button";
import { Typography } from "@mui/material";
import IosShareIcon from "@mui/icons-material/IosShare";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";

export default function ModalS({ facebook, instagram }) {
  const [open, setOpen] = React.useState(false);

  React.useEffect(() => {
    if (facebook === true || instagram === true) {
      setOpen(true);
    }
  }, []);

  const handleClose = () => {
    setOpen(false);
  };

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    pt: 2,
    px: 4,
    pb: 3,
  };

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style}>
        {facebook === true && (
          <>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              bnet i Facebook in-app browser
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              For en bedre oplevelse tryk p ikonet <IosShareIcon /> i hjre
              hjrne og vlg "bn i Safari"
            </Typography>
          </>
        )}
        {instagram === true && (
          <>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              bnet i Instagram in-app browser
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              For en bedre oplevelse tryk p ikonet <MoreHorizIcon /> i hjre
              hjrne i toppen og vlg "bn i browser"
            </Typography>
          </>
        )}
      </Box>
    </Modal>
  );
}
